export function friendlyDate(datsStr) {
    let dateObj = typeof datsStr === 'object' ? datsStr : new Date(datsStr)
    let time = dateObj.getTime()
    let now = Date.now()
    let space = now - time
    let str = ''

    // console.log('1')
    switch (true) {
        //
        case space < 60000:
            str = ''
            break
        //
        case space < 1000 * 3600:
            str = Math.floor(space / 60000) + ''
            break
        //
        case space < 1000 * 3600 * 24:
            str = Math.floor(space / (1000 * 3600)) + ''
            break
        default:
            str = Math.floor(space / (1000 * 3600 * 24)) + ''
    }
    return str
} 


